import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { Observable } from 'rxjs';

@Injectable({
  providedIn: 'root'
})
export class ApiserviceService {

  constructor(public http: HttpClient) { }

  get_all_blogs():Observable<any> {
    return this.http.get<any>(`https://api.learnerbeee.com/v1/get_blogs/`, {responseType: 'json'});
  }

  get_popular_post_blogs():Observable<any> {
    return this.http.get<any>(`https://api.learnerbeee.com/v1/get_popular_blogs/`, {responseType: 'json'});
  }

  get_blog_by_url(content_url):Observable<any> {
    return this.http.get<any>(`https://api.learnerbeee.com/v1/blog_by_url/?content_url=` + content_url, {responseType: 'json'});
  }
}
