import { Component, OnInit } from '@angular/core';
import { ApiserviceService } from 'src/app/services/apiservice.service';
import { Router } from '@angular/router';

@Component({
  selector: 'app-home',
  templateUrl: './home.component.html',
  styleUrls: ['./home.component.css']
})
export class HomeComponent implements OnInit {

  articles = []

  pupular_post = []

  constructor(public api: ApiserviceService, public router:Router) { }

  ngOnInit(): void {
    this.api.get_all_blogs().subscribe(
      (data) => {
        console.log(data)
        this.articles = data["payload"];
      }
    )

    this.api.get_popular_post_blogs().subscribe(
      (data) => {
        console.log(data)
        this.pupular_post = data["payload"];
      }
    )
  }

  navigateToPost(url) {
    this.router.navigate(['/articles/' + url]);
    console.log(url);
  }

}
