import { Component, OnInit } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { ApiserviceService } from 'src/app/services/apiservice.service';
import { ViewEncapsulation } from '@angular/core';

@Component({
  selector: 'app-article',
  templateUrl: './article.component.html',
  styleUrls: ['./article.component.css'],
  encapsulation: ViewEncapsulation.None,
})
export class ArticleComponent implements OnInit {
  blog_id = "";
  post = {
    "title": "",
    "description": "",
    "image": "",
    "author": {
      "name": "",
      "email": "",
      "profile_pic": ""
    },
    "updated": "",
    "content": "",
    "index": []
  }


  popular_post = []
  constructor(public route: ActivatedRoute, public api: ApiserviceService, public router:Router) { 
    this.route.params.subscribe(
      (data) => {
        this.blog_id = data.article_url
      },
      error => {

      }
    )
   }

  ngOnInit(): void {
    this.api.get_blog_by_url(this.blog_id).subscribe(
      (data) => {
        this.post = data["payload"];
        console.log(this.post);
      }
    )

    this.api.get_popular_post_blogs().subscribe(
      (data) => {
        console.log(data)
        this.popular_post = data["payload"];
      }
    )
  }

  navigateToPost(url) {
    this.router.navigate(['/articles/' + url]);
    console.log(url);
  }

}
